import React from 'react';
import { Field } from 'redux-form';

import { Box, Flex } from '@rebass/grid';

import { CheckboxField, Delimiter, Hr, NumberFormatField, SelectField } from 'components';
import { loanInterestConst, loanTypesOptions } from 'consts';
import { ISelectValue } from 'types';
import { formErrorUtil } from 'utils';

// const numberOfInstalmentsValidation = formErrorUtil
//   .lessThanValue('defaultInstalments', 'Must be < "Default Instalments"');

interface ILoanDetails {
  isReadOnly?: boolean;
  interestDistributionValue: ISelectValue;
}

const LoanDetails: React.FC<ILoanDetails> = ({
  isReadOnly,
  interestDistributionValue,
}) => {
  const isEqualOrActualDistribution = React.useMemo(
    () => interestDistributionValue
      && (interestDistributionValue.value === loanInterestConst.REDUCING_BALANCE_METHOD_SIMPLE
        || interestDistributionValue.value === loanInterestConst.REDUCING_BALANCE_METHOD_COMPOUND
        || interestDistributionValue.value === loanInterestConst.FLAT_RATE_METHOD),
    [interestDistributionValue]
  );

  return (
    <Box mx="-8px">
      <Flex
        alignItems="flex-end"
        flexWrap="wrap"
      >
        <Box
          width="320px"
          p="8px"
        >
          <Field
            id="interestDistributionType"
            name="interestDistributionType"
            component={SelectField}
            label="Interest Method"
            placeholder="Select Method"
            options={loanTypesOptions}
            isDisabled={isReadOnly}
            validate={[formErrorUtil.isRequired]}
          />
        </Box>
        <Delimiter />
        <Box
          width="160px"
          p="8px"
        >
          <Field
            id="minLoan"
            name="minLoan"
            component={NumberFormatField}
            placeholder="0.00"
            fixedDecimalScale={true}
            decimalScale={2}
            label="Min Loan"
            disabled={true}
            isNumber={true}
          />
        </Box>
        <Box
          width="160px"
          p="8px"
        >
          <Field
            id="maxLoan"
            name="maxLoan"
            component={NumberFormatField}
            placeholder="0.00"
            fixedDecimalScale={true}
            decimalScale={2}
            label="Max Loan"
            disabled={true}
            isNumber={true}
          />
        </Box>
        <Box
          width="160px"
          p="8px"
        >
          <Field
            id="minInstalments"
            name="minInstalments"
            component={NumberFormatField}
            placeholder="0"
            label="Min Instalments"
            disabled={true}
            isNumber={true}
          />
        </Box>
        <Box
          width="160px"
          p="8px"
        >
          <Field
            id="maxInstalments"
            name="maxInstalments"
            component={NumberFormatField}
            placeholder="0"
            label="Max Instalments"
            disabled={true}
            isNumber={true}
          />
        </Box>
        <Hr />
        <Box
          width="160px"
          p="8px"
        >
          <Field
            id="defaultInstalments"
            name="defaultInstalments"
            component={NumberFormatField}
            placeholder="0"
            label="Default Instalments"
            disabled={true}
            isNumber={true}
          />
        </Box>
        {isEqualOrActualDistribution && (
          <Box
            width="160px"
            p="8px"
          >
            <Field
              id="defNumInterestFreeInstlmts"
              name="defNumInterestFreeInstlmts"
              component={NumberFormatField}
              placeholder="0"
              label="Default # of Interest Free Instalments"
              disabled={isReadOnly}
              isNumber={true}
              validate={[
                formErrorUtil.isRequired,
                formErrorUtil.isInteger,
              ]}
            />
          </Box>
        )}
        <Box
          width="160px"
          p="8px"
        >
          <Field
            id="defNumDeferredInstlmts"
            name="defNumDeferredInstlmts"
            component={NumberFormatField}
            placeholder="0"
            label="Default # of Deferred Instalments"
            disabled={isReadOnly}
            isNumber={true}
            validate={[
              formErrorUtil.isRequired,
              formErrorUtil.isInteger,
            ]}
          />
        </Box>
        <Hr />
        <Box
          width="160px"
          p="8px"
        >
          <Field
            id="postingDelay"
            name="postingDelay"
            component={NumberFormatField}
            placeholder="0"
            label="Posting Delay"
            disabled={isReadOnly}
            isNumber={true}
            validate={[
              formErrorUtil.isRequired,
              formErrorUtil.isInteger,
            ]}
          />
        </Box>
        <Box
          width={[1]}
          p="8px"
        >
          <Field
            id="allowOverpayment"
            name="allowOverpayment"
            component={CheckboxField}
            label="Allow overpayment"
            disabled={isReadOnly}
          />
        </Box>
        <Box
          width={[1]}
          p="8px"
        >
          <Field
            id="allowHolidays"
            name="allowHolidays"
            component={CheckboxField}
            label="Allow Holidays"
            disabled={isReadOnly}
          />
        </Box>
      </Flex>
    </Box>
  );
};

export default LoanDetails;
